import services from '../services'
import React from 'react'

const DisplayAll = (props) => {

    const deleteHandler = (id,e) => {
        console.log(id)
        const person = props.persons.find(person => person.id === id)
        if (window.confirm(`Will you delete ${person.name}'s contact?`)) {
            services.deletePerson(id)
            .then(res => {
                const newList = props.persons.filter(person => person.id !== id)
                props.setPersons(newList)
            });
        };        
    };

    return (
        <div>
            <h2>Numbers</h2>
            {props.persons.map((value,index) => 
            <li key={value.id}>{value.name} {value.number} <button type="submit" onClick ={(e) => deleteHandler(value.id,e)}>delete</button></li>)}
        </div>
    )
}

export default DisplayAll

