import React, { useEffect,useState } from 'react'
import {Filter} from './components/filterComponent'
import {FilterResults} from './components/filterComponent'
import AddPerson from './components/addComponent'
import DisplayAll from './components/displayAllComponent'
import { FilterCountry , FilterCountryResult} from './components/countryComponent'
import services from './services'
import Notification from './components/notificationComponent'

const App = () => {
  const [ persons, setPersons ] = useState([])
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ newFilter, setNewFilter ] = useState('')
  const [ countries , setCountries ] = useState([])
  const [ newCountryFilter, setNewCountryFilter ] = useState('')
  const [ newSuccessMessage, setSuccessMessage] = useState(null)

  useEffect(() => {
    services.getPersons()
    .then(response => {
      setPersons(response.data)
    });
  },[persons])

  useEffect(() => {
    services.getCountries()
    .then(response => {
      setCountries(response.data)
    });
  },[])

  return (
    <div>
      <h2>Phonebook</h2>
      <br></br>
      <Filter newFilter = {newFilter} setNewFilter = {setNewFilter} />
      <br></br>
      <FilterResults persons = {persons} newFilter = {newFilter} />
      <br></br>
      <Notification message = {newSuccessMessage} />
      <br></br>
      <AddPerson persons = {persons} setPersons = {setPersons} newName = {newName} 
      setNewName = {setNewName} newNumber = {newNumber} setNewNumber = {setNewNumber} setSuccessMessage = {setSuccessMessage} />
      <br></br>
      <DisplayAll persons = {persons} setPersons = {setPersons} />
      <br></br>
      <FilterCountry newCountryFilter = {newCountryFilter} setNewCountryFilter = {setNewCountryFilter} />
      <br></br>
      <FilterCountryResult countries = {countries} newCountryFilter = {newCountryFilter} />
      <br></br>
    </div>
  )

}

export default App