import React from 'react'


const Filter = (props) => {

    const handleFilterChange = (event) => {
      console.log(event.target.value);
      props.setNewFilter(event.target.value);
    }
      return(
        <div>
          <form>
            <div>
              filter: <input value={props.NewFilter} onChange = {handleFilterChange} />
            </div>
          </form>
        </div>
      )
  }


const FilterResults = (props) => {  

if (props.persons.some(person=> person.name === props.newFilter)) {
    let filteredName = props.persons.find(person => person.name === props.newFilter).name
    let filteredNumber = props.persons.find(person => person.name === props.newFilter).number

    return (
        <div>
              Result: {filteredName} {filteredNumber}
        </div>
      )
    } else {
    return (
        <div>Result: </div>
    )    
    }
}
export {
    Filter,
    FilterResults
}