import React from 'react'

const FilterCountry = (props) => {
    
    const handleCountryFilter = (event) => {
        console.log(event.target.value);
        props.setNewCountryFilter(event.target.value)
    }


    return (
        <div>
            <form>
                <div>
                    filter: <input value = {props.newCountryFilter} onChange = {handleCountryFilter} />
                </div>
            </form>
        </div>
    )
}

const FilterCountryResult = (props) => {

    const filteredCountry = props.countries.filter(country => country.name.includes(props.newCountryFilter))
    
    if (props.newCountryFilter === '') {
        return (
        <div></div>
        )
    }

    else if (filteredCountry.length > 10) {
        return (
            <div>
                Too many matches. Be more specific
            </div>
        )
    }

    else if (filteredCountry.length <=10 && filteredCountry.length > 1) {
        return ( 
            <div>
                {filteredCountry.map((value,index) =>
                <li key = {value.name}>{value.name}</li>)}
            </div>
        )
    }
    
    else if (filteredCountry.length === 1) {
        return (
            <div>
                {filteredCountry.map((value,index) =>
                <div>
                <h2 >{value.name}</h2>
                <br></br>
                <div>capital {value.capital}</div>
                <div>population {value.population}</div>
                <br></br>
                <h2>languages</h2>
                <br></br>
                <div> {value.languages.map((name,index) =>
                <li>{name.name}</li>)}
                </div>
                <img alt = 'flag' height= "100" width = "100" style= {{border:`solid 1px`}} src={value.flag}></img>
                </div>
                )}
                
            </div>

        )
    }

    else return (
        <div>
            
        </div>
    )
}

export {
    FilterCountry,
    FilterCountryResult
}